import React, { useState, useEffect } from 'react';
import Footer from './components/Footer';
import Note from './components/Note';
import Notification from './components/Notification';
import noteService from './services/note';

const App = () => {
  const [notes, setNotes] = useState([]);
  const [newNote, setNewNote] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [errorMessage, setErrorMessage] = useState(null);
  
  const hook = () => {
    const nonExisting = {
      id: 10000,
      content: 'This note is not saved to server',
      date: '2019-05-30T17:30:31.098Z',
      important: true,
    }
    noteService.getAll().then((data) => setNotes(data.concat(nonExisting)))
  }
  useEffect(hook, []);

  const addNote = e => {
    e.preventDefault();
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
    }
    const handleRes = data => {
      setNotes(notes.concat(data));
      setNewNote('');
    }
    noteService.create(noteObject).then(handleRes);
  }
  
  const handleNoteChange = e => setNewNote(e.target.value);
  
  const toggleImportanceOf = note => {
    const handleRes = (data) => {
      setNotes(notes.map(item => item.id === note.id? data:item))
    }
    const changedNote = {...note, important: !note.important};
    noteService.update(note.id, changedNote).then(handleRes)
    .catch(err => {
      setErrorMessage(`the note ${note.content} non exist on the server`);
      setTimeout(() => {
        setErrorMessage(null)
      }, 3500)
      setNotes(notes.filter(item => item.id !== note.id));
    });
  }

  const notesToShow = showAll? notes : notes.filter((note) => note.important );

  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage}/>
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          Show {showAll? 'only important':'all'}
        </button>
      </div>
      
      <ul>
        {
          notesToShow.map(item => {
            return <Note
              key={item.id}
              note={item}
              toggleImportance={() => {toggleImportanceOf(item)}}
            />
          })
        }
      </ul>

      <form onSubmit={addNote}>
        <input onChange={handleNoteChange} placeholder='new note' value={newNote}required/>
        <button type='submit'>Save Note</button>
      </form>
      <Footer/>
    </div>
  )
}
export default App;
